import React from 'react';

import firebase from 'firebase/app';

class CreateSelection extends React.Component {
  constructor(props) {
    super(props);
    this.createSelection = this.createSelection.bind(this);
  }

  createSelection() {
    const { category, content, answer } = this.refs;
    firebase.database().ref(`categories/${category.value}`).push({
      content: content.value,
      answer: answer.value
    });
  }

  render() {
    return (
      <div>
        Category: <input ref="category" />
        Content: <input ref="content" />
        Answer: <input ref="answer" />
        <button onClick={this.createSelection}>Create Selection</button>
      </div>
    );
  }
}

export default CreateSelection;



