import React, { PropTypes } from 'react';
import { Match } from 'react-router'
import { connect } from 'react-redux';

import Beer from './beer/Beer';
import Cheese from './cheese/Cheese';
import Whiskey from './whiskey/Whiskey';

import fetchCategory from './fetchCategory';

const contentStyle = {
  width: '100%',
  textAlign: 'center'
};

class CategoryContent extends React.Component {
  render() {
    const { pathname } = this.props;
    return (
      <div className="category-container">
        <Match pattern={`${pathname}/beer`} component={fetchCategory(Beer, 'beer')} />
        <Match pattern={`${pathname}/cheese`} component={fetchCategory(Cheese, 'cheese')} />
        <Match pattern={`${pathname}/whiskey`} component={fetchCategory(Whiskey, 'whiskey')} />
      </div>
    );
  }
}

export default CategoryContent;



