import React, { PropTypes } from 'react';
import { HashRouter, Match, Miss } from 'react-router'
import { connect } from 'react-redux';
import { initialize as firebaseInitialize } from './firebase/actions';

// app wide
import AppHeader from './AppHeader';
import AppSideNav from './AppSideNav';

// routes
import Home from './Home';
import Login from './Login';
import Register from './Register';

import Profile from './user/Profile';
import CategoryContainer from './categories/CategoryContainer';

const contentStyle = {
  width: '100%',
  textAlign: 'center'
};

class App extends React.Component {
  constructor(props) {
    super(props);
    if (typeof window !== 'undefined') {
      this.props.dispatch(firebaseInitialize());
    }
  }
  
  getChildContext() {
    return {
      router: this.props.router
    };
  }

  render() {
    return (
      <div className="app">
        <AppHeader />
        <div className="app-content-container" style={contentStyle}>
          <AppSideNav />
          <div className="content-container" style={{ display: 'inline-block' }}>
            <Match exactly pattern="/" component={Home} />
            <Match exactly pattern="/register" component={Register} />
            <Match exactly pattern="/login" component={Login} />
            <Match pattern="/profile/:uid" component={Profile} />
            <Match pattern="/categories" component={CategoryContainer} />
          </div>
        </div>
      </div>
    );
  }
}

App.childContextTypes = {
  router: React.PropTypes.object
};

const mapStateToProps = ({ user }) => ({ user });

export default connect(mapStateToProps)(App);



