import React from 'react';

class Cheese extends React.Component {
  render() {
    const { picks } = this.props;
    const pickEls = picks.map((pick) => {
      return <div key={pick.key}>
        {pick.content}
      </div>
    });
    return (
      <div className="category-cheese">
        <h2>Cheese</h2>
        {pickEls}
      </div>
    );
  }
}

export default Cheese;



