import React from 'react';
import RegisterForm from './user/RegisterForm';

const style = {
  textAlign: 'center'
};

class Register extends React.Component {
  render() {
    return (
      <div className="register" style={style}>
        <RegisterForm />
      </div>
    );
  }
}

export default Register;



