import React from 'react';

class Beer extends React.Component {
  render() {
    const { picks } = this.props;
    const pickEls = picks.map((pick) => {
      return <div key={pick.key}>
        {pick.content}
      </div>
    });
    return (
      <div className="category-beer">
        <h2>Beer</h2>
        {pickEls}
      </div>
    );
  }
}

export default Beer;



