import React from 'react';
import { connect } from 'react-redux';
import hashToArray from '../util/hashToArray';

import firebase from 'firebase/app';

function fetchCategory(WrappedComponent, category) {
  return class FetchCategory extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        picks: []
      };
    }

    componentDidMount() {
      firebase.database().ref(`categories/${category}`).on('value', (picks) => {
        const val = picks.val();
        if (this.unMounting !== true && val) {
          this.setState({
            picks: val
          });
        }
      });
    }

    componentWillUnmount() {
      this.unMounting = true;
    }

    render() {
      const picks = hashToArray(this.state.picks);
      return (
        <WrappedComponent className="picks-container" picks={picks}/>
      );
    }
  }
}

export default fetchCategory;



