import firebase from 'firebase/app';
import 'firebase/database';

const FIREBASE_INIT = 'FIREBASE_INIT';

const config = {
  apiKey: "AIzaSyCGRnpjSG2NqZOth-NkHcVTnhzBwq0Rx2k",
  authDomain: "basically-fire.firebaseapp.com",
  databaseURL: "https://basically-fire.firebaseio.com",
  storageBucket: "basically-fire.appspot.com",
  messagingSenderId: "109745830181"
};

function initialize() {
  return (dispatch) => {
    dispatch({ type: FIREBASE_INIT });
    firebase.initializeApp(config);
  }
}


export {
  FIREBASE_INIT,
  initialize,
};



