import React from 'react';

class Whiskey extends React.Component {
  render() {
    const { picks } = this.props;
    const pickEls = picks.map((pick) => {
      return <div key={pick.key}>
        {pick.content}
      </div>
    });
    return (
      <div className="category-whiskey">
        <h2>Whiskey</h2>
        {pickEls}
      </div>
    );
  }
}

export default Whiskey;



