import React, { PropTypes } from 'react';
import { Link } from 'react-router';
import { connect } from 'react-redux';

const style = {
  display: 'inline-block',
  position: 'absolute',
  left: '0px'
};

class AppSideNav extends React.Component {
  render() {
    return (
      <ul className="category-nav" style={style}>
        <li><Link to="/">Home</Link></li>
        <li><Link to="/categories/beer">Beer</Link></li>
        <li><Link to="/categories/cheese">Cheese</Link></li>
        <li><Link to="/categories/whiskey">Whiskey</Link></li>
      </ul>
    );
  }
}

export default AppSideNav;



