import axios from 'axios';

const ROOT_URL = 'http://localhost:3000'

const CLIENT_LOGIN = 'CLIENT_LOGIN';
const LOGIN = 'LOGIN';
const LOGOUT = 'LOGOUT';

const logIn = (user) => ({
  type: LOGIN,
  user
});

const clientLogIn = ({ username, password }) => {
  return (dispatch, getState) => axios
    .post(`${ROOT_URL}/login`, { username, password })
    .then((response) => {
      console.log(response);
    });
};

const logOut = () => ({
  type: LOGOUT
});

export {
  CLIENT_LOGIN,
  LOGIN,
  LOGOUT,
  clientLogIn,
  logIn,
  logOut
};



