import React, { Component, PropTypes } from 'react';
import LoginForm from './user/LoginForm';

const style = {
  textAlign: 'center'
};

class Login extends Component {
  render() {
    return (
      <div className="login" style={style}>
        <LoginForm />
      </div>
    );
  }
}

Login.propTypes = {
  user: PropTypes.object
};

export default Login;



